import { createAction } from 'redux-actions';
import { apiService } from '../../api/service';
import { getAllCategories } from '../categories/actions';

export const setCars = createAction('SET_CARS');
export const setStatus = createAction('SET_CARS_STATUS');
export const setFilter = createAction('SET_CARS_FILTER');
export const cleanupCars = createAction('CLEANUP_CARS');

// Добавить обработку ошибки!!!
export const getAllCars = ({ isInitial }) => async (dispatch, getState) => {
  const { dataFiltred, status } = getState().cars;
  if (status === 'received' && dataFiltred === false) return;

  dispatch(setStatus({ status: isInitial ? 'initial' : 'fetching' }));
  try {
    const responseCars = await apiService.getCars();
    const cars = responseCars.data.data;
    dispatch(setCars({
      data: cars,
      status: 'received',
      dataFiltred: false,
    }));
  } catch (e) {
    dispatch(setStatus({ status: 'error' }));
    throw new Error('Ошибка получения данных');
  }
};

const getFiltredCars = (newFilter) => async (dispatch) => {
  const filters = { category: newFilter.category };
  dispatch(setFilter({ filters }));

  dispatch(setStatus({ status: 'fetching' }));
  try {
    const responseCars = await apiService.getCars({ categoryId: newFilter.category });
    const cars = responseCars.data.data;
    dispatch(setCars({
      data: cars,
      status: 'received',
      dataFiltred: true,
    }));
  } catch (e) {
    dispatch(setStatus({ status: 'error' }));
    throw new Error('Ошибка получения данных');
  }
};

export const initialCarsRequest = () => async (dispatch) => {
  const responses = [
    dispatch(getAllCars({ isInitial: true })),
    dispatch(getAllCategories()),
  ];

  Promise.all(responses)
    .then()
    .catch((e) => {
      console.log(e);
    });
};

export const carsRequest = (newFilter) => async (dispatch) => {
  try {
    if (Object.values(newFilter).every((el) => el === null)) {
      dispatch(getAllCars({ isInitial: false }));
    } else {
      dispatch(getFiltredCars(newFilter));
    }
  } catch (e) {
    console.log(e);
  }
};

export const cleanupIfNeed = () => async (dispatch, getState) => {
  const { dataFiltred, status } = getState().cars;
  if (status === 'received' && dataFiltred === false) return;

  dispatch(cleanupCars());
};
